<template>
    <div>
        <!-- 数据部分 -->
        <div class="box-container">
            <div class="box-btn">
                <h4>数据模型</h4>
            </div>
            <div class="box-btn">
                <el-button type="success" @click="addModel">添加模型</el-button>
            </div>
            <el-table
                v-loading="loading"
                :data="modelList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column prop="id" width="80" align="center" label="序号"></el-table-column>
                <!-- <el-table-column prop="data" align="center" label="数据"></el-table-column> -->
                <el-table-column prop="name" align="center" label="模型名称"></el-table-column>
                <el-table-column prop="description" align="center" label="模型描述"></el-table-column>
                <el-table-column prop="url" align="center" label="模型调用地址(url)"></el-table-column>
                <el-table-column align="center" label="模型使用状态">
                <template slot-scope="{row}">
                        <el-tag :type="row.status | statusType">{{ row.status | statusVal }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" @click="onEdit(scope.row)">编辑</el-button>
                        <el-button size="mini" type="danger" @click="onDel(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <!-- 添加数据 -->
        <AddModel titleText="添加数据模型" 
            :oneData="form"
            :dialogFormVisible="isdialog2"
            @setData="addMyData"
            @setDialog="getDialog"
        ></AddModel>

        <!-- 编辑模型 -->
        <AddModel titleText="编辑数据模型" 
            :oneData="oneData"
            :dialogFormVisible="isdialog"
            @setData="getData"
            @setDialog="getDialog"></AddModel>

        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
    </div>
</template>

<script>

import Pagination from '@/components/Pagination.vue';
import AddModel from './AddModel'

export default {
    components:{
        Pagination,
        AddModel
    },
    filters:{
        statusType(type){
            const typeMap={
                true:'success',
                false:'info'
            }
            return typeMap[type];
        },
        statusVal(val){
            const valMap={
                true:'发布',
                false:'未发布'
            }
            return valMap[val];
        }
    },
    data(){
        return{
            oneData:{},
            isdialog2:false,
            isdialog:false,
            loading:false,
            modelList:[],
            name:'',
            status:'',
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            },
            form:{
                data:'',
                description:'',
                status:'',
                name:'',
                url:''
            }
        }
    },
    methods:{
        addModel(){//添加模型
            this.isdialog2 = true;
        },
        addMyData(data){//发布数据
            let that = this;
            this.$api.postal({
                data: data.data,
                name: data.name,
                description:data.description,
                status:data.status,
                url:data.url
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'模型发布成功',
                        type:'success'
                    })
                    that.isdialog2 = false;
                    that.getModelList();
                }else{
                    that.$message({
                        message:'模型发布失败',
                        type:'error'
                    })
                }
            })
        },
        onEdit(info){//编辑
            this.isdialog=true;
            this.oneData = info;
        },
        getData(data){
            let that = this;
            this.$api.putadmuser(`${data.id}`,{
                data: data.data,
                name: data.name,
                description:data.description,
                status:data.status,
                url:data.url
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'模型修改成功',
                        type:'success'
                    })
                    that.isdialog = false;
                    that.getModelList();
                }else{
                    that.$message({
                        message:'模型修改失败',
                        type:'error'
                    })
                }
            })
        },
        getDialog(data){
            this.isdialog2 = data;
            this.isdialog = data;
            this.form.data='';
            this.form.description = '';
            this.form.status = '';
            this.form.name='';
            this.form.url='';
        },
        onDel(info){//删除
            let that = this;
            this.$confirm('确定删除吗?','提示',{
                type:'warning'
            }).then(()=>{
                that.$api.deleteal(`${info.id}`).then(res=>{
                    if(res.err_code === 100){
                        that.$message({
                            message:'删除成功',
                            type:'success'
                        })
                        that.getModelList();
                    }else{
                        that.$message({
                            message:'删除失败',
                            type:'error'
                        })
                    }
                })
            }).catch(()=>{})   
        },
        getModelList(){//获取模型数据
            let that =this;
            this.$api.getal({
                name:that.name,
                page_num:that.query.pageIndex,
                page_size:that.query.pageSize,
                status:that.status
            }).then(res=>{
                if(res.err_code === 100){
                    let data = res.data.list;
                    that.modelList = data;
                    that.query.total = data.length;
                }
            })
        },
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getModelList();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getModelList();
        }
    },
    created(){
        this.getModelList();
    }
}
</script>

<style lang="scss" scoped>
    .box-btn{
        margin: 10px 0px;
    }
    .box-container{
        margin: 10px 10px 20px;
    }
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
</style>