<template>
    <div>
        <el-dialog :title="titleText"
            @close="closeDialog"
            :visible.sync="dialogFormVisible_">
            <el-form :model="oneData">
                <el-form-item label="模型名称" :label-width="formLabelWidth">
                    <el-input v-model="oneData.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="模型使用数据说明" :label-width="formLabelWidth">
                    <el-input v-model="oneData.data" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="模型描述" :label-width="formLabelWidth">
                    <el-input v-model="oneData.description" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="模型调用url地址" :label-width="formLabelWidth">
                    <el-input v-model="oneData.url" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="模型发布状态" :label-width="formLabelWidth">
                    <el-select v-model="oneData.status" placeholder="请选择发布状态">
                        <el-option label="发布" value="true"></el-option>
                        <el-option label="未发布" value="false"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button  @click="cancelDialog">取 消</el-button>
                <el-button type="primary"  @click="addData('oneData')">添 加</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props:{
        titleText:{type:String},
        oneData:{type:Object},
        dialogFormVisible:{ type:Boolean,default:false},
        formLabelWidth:{ type:String,default:'150px'}
    },
    computed:{
        dialogFormVisible_:{
            get(){
                return this.dialogFormVisible
            },
            set(val){
                this.$emit('setDialog',val);
            }
        }
    },
    methods:{
        closeDialog(){
            this.dialogFormVisible_ = false;
        },
        cancelDialog(){
            this.dialogFormVisible_ = false;
        },
        addData(){
            let that =this;
            if(!that.oneData.name || !that.oneData.url || !that.oneData.status ||
                that.oneData.description || that.oneData.data){
                return that.$message({
                    message:'所填数据不能为空',
                    type:'error'
                })
            }
            this.$emit('setData',this.oneData);
        }
    }
}
</script>

